.header-color {
	background: #833ab4;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #072d97, #142342,#162649, #0c2d8a); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
  
  .landing-grid {
	background: #5b575e;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #bd1d5a, #631616, #090a6d);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #726d6f, #969092,#948e90, #726d6f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
  }
  
  .projects-grid {
	display: flex;
  }
  
  /* Resume Page css */
  .resume-right-col {
	background: #27221F;
	color: white;
	padding: 2em;
  }
  
  /* contact page css */
  
  .contact-body {
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 2em;
  }
  
  .contact-grid {
	text-align: center;
	justify-content: center;
	width: 80%;
	height: 550px;
	background: white;
	color: black;
	margin-bottom: 20px;
  }
  
  .contact-grid h2 {
	font-family: 'Anton', sans-serif;
  }
  
  .contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold;
  }
  
  .contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
  }
  
  .contact-list i {
	font-size: 66px;
	padding-right: 5rem;
  }
  
  
  /* contact page css */
  
  .avatar-img {
	height: 250px;
	padding-top: 5em;
  }
  
  .banner-text {
	background-color: rgb(4, 17, 59);
	opacity: .8;
	width: 75%;
	margin: auto;
	border-radius: 10px;
  }
  
  .banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: white;
  }
  
  .banner-text hr {
	border-top: 5px dotted white;
	width: 50%;
	margin: auto;
  }
  
  .banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
  }
  
  .social-links {
	display: flex;
	justify-content: space-between;
	width: 50%;
	margin: auto;
  }
  
  .social-links i {
	color: white;
	font-size: 5em;
	padding: 8px;
  }
  
  .sub-header {

    background: #baacc4;  /* fallback for old browsers */
	  background: -webkit-linear-gradient(to right, #837a6d, #b86c6c, #a37dbd);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #524636, #6e4f4f, #655570); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: flex;
    padding: 0px;
    margin: 0px;

}
.content{
background-color:rgb(83, 16, 16);
width: 100%;
height: 900px;

}

